export const REGEX_EMAIL = /^\S+@\S+\.\S+$/;

export const REGEX_PHONE = /^\+?[1-9][0-9]{7,14}$/;

export const REGEX_FULL_NAME =
    /^[A-Z][a-z]*(?:[ ][A-Z][a-z]*)*$/gm;

export const REGEX_PASSWORD = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/g;
